<!-- Registtration -->
<template>
<div class="login-background">
  <div class="sign_up">
     <div class="logo">
         <center><img src="../assets/images/laravel-vapor.svg" alt="logo"></center>
     </div>
     <div class="form">
         <form>
          <label>Name</label><br>
          <input type="text"><br>
          <label>Password</label><br>
          <input type="password"><br>
          <div class="rememberandcheck">
          <div>
          <input class="form-checkbox" type="checkbox" name="remember"></div>
          <div>
          <p class="remembermetext">Remember me</p></div>
          </div>
          <div class="forgetpasssubmit">
            <div><div class="submit">LOG IN</div></div>
            <div class="forgetpass">Forget Password?</div>
          </div>
          <div class="alreadyregistered">
            <p>Have already registered? <span> <router-link to="/login">LOG IN </router-link></span></p>
          </div>
         </form>
     </div>
  </div>
  
   
</div>
</template>

  <script>
export default {
  
  data () {
    return {

    }
  }
   
}
  </script>

  <style scope>
*{
    margin: 0;
    padding: 0;

}

.login-background{
    background-color: #edf2f7;
    width:100%;
    height: 100vh;
    position: relative;
   
}

.sign_up{
    position: absolute;
    top:50%;
    left: 50%;
    transform: translate(-50%,-50%);
    box-sizing: border-box;
    width: 400px;
}
.sign_up .logo{
      width: 60px;
      height:50px;
      margin:0 auto 0 auto;
  }
  .sign_up .logo img{
     overflow: hidden;
     height:100%;
     width:100%;
  }
  .form{
     padding: 20px 30px 20px 20px;
     background-color: white;
     border-radius: 8px;
     margin-top: 20px;
  }
  .form input[type="text"],.form input[type="password"]{
      width:100%;
      padding: 5px 5px;
      border-color: #e2e8f0;
      line-height: 30px;
      border-width: 1px;
  }
  .form-checkbox{
      display: inline-block;
      /* margin-top: 17px; */
  }
  .form form label{
      display: flex;
      margin-top: 10px;
  }
  .rememberandcheck{
      display: flex;
      flex-direction: row;
      margin-top: 15px;
  }
  .remembermetext{
      margin-left: 20px;
  }
  .forgetpasssubmit{
      display: flex;
      flex-direction: row-reverse;
      margin-top: 10px;
  }
  .forgetpass{
      color: #718096;
      margin-top: 5px;
  }
  .submit{
      background-color: #2d3748;
      color: white;
    padding: 10px 20px;
    border-radius: 5px;
    margin-left: 20px;
  }
  .alreadyregistered{
      margin-top: 20px;
  }
  .alreadyregistered span{
      color:blue;
      display: inline-block;
      font-size: 14px;
      font-weight: 600;
  }
  .alreadyregistered span a{
      text-decoration: none;
      color: blue;
  }
  .alreadyregistered span a:hover{
      text-decoration: underline;
       color: blue;
  }

  </style>


